  .navbar-homepage {
    display: flex;
    justify-content: space-between;
    margin: 3rem 1rem 0rem 1rem;
  }
  .my-name-homepage, .title-homepage {
    font-size: 32px;
    font-weight: 900;
  }
  .change-my-name-homepage-btn {
    color: #FF6464;
    border: none;
    font-size: 14px;
    font-weight: 900;
  }
  .my-name-homepage-show {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%);
  }
  .change-my-name-homepage-show {
    display: block;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%);
    animation: change-name-input .2s alternate;
    z-index: +1;
  }
  .change-my-name-homepage {
    display: none;
  }
  .title {
    display: flex;
  }
  .my-name-homepage {
    margin-left: .5rem;
  }
  .music-homepage {
    width: 100%;
    height: 4rem;
    background-color: #ffff;
    border-radius: 16px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding-right: 1rem;
    align-items: center;
    margin-bottom: .75rem;
  }
  .music-homepage-logo img {
    width: 4rem;
    height: 4rem;
    border-radius: 16px 0 0 16px;
    display: flex;
    align-self: center;
    flex: 1;
  }
  .musics-homepage {
    margin-top: 2rem;
  }
  .music-homepage-song-singer-play {
    flex: 2;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: .75rem;
  }
  .music-homepage-singer {
    font-size: 12px;
    margin-top: .25rem;
  }
  .music-homepage-song {
    font-size: 16px;
  }
  .music-homepage-play button {
    color: #F05A4D;
    font-weight: 900;
    font-size: 14px;
    background-color: #ffff;
    border: none;
    width: 4rem;
    height: 2rem;
    border-radius: 8px;
  }
  .music-homepage-play button:hover {
    background-color: #F05A4D;
    color: #f0f0f0;
    scale: 1.05;
    animation: play-btn-hover .3s alternate;
  }
  @keyframes change-name-input {
    0% {
      display: none;
      opacity: 0;
    }
    100% {
      display: block;
      opacity: 100%;
    }
  }
  @keyframes play-btn-hover {
    0% {
      color: #F05A4D;
      background-color: #ffff; 
      scale: 1; 
    }
    100% {
      background-color: #F05A4D;
      color: #f0f0f0;
      scale: 1.05;  
    }
  }
}